* {
  box-sizing: border-box;
}

body {
  margin: 0;
  font-family: "Barlow", sans-serif;
  font-optical-sizing: auto;
  font-weight: 500;
  font-style: normal;
  font-variation-settings: "wdth" 100;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #ddd;
}

p {
  margin-top: 0;
}

.container {
    max-width: 850px;
    margin: 0 auto;
    padding: 30px;
    background-color: #37474f;
    min-height: 100vh;
}

h2 {
  font-size: 22px;
  color: #0053bd;
}

.row {
  display: flex;
  flex-wrap: wrap;
}

.two-col {
  width: 100%;
  padding: 20px;
}

.last-update {
  text-align: right;
  color: #ddd;
  font-weight: 200;
  font-size: 14px;
}

.info-box {
  display: flex;
  align-items: center;
  justify-content: center;
  color: #fff;
  font-size: 20px;
  font-weight: normal;
  padding: 12px;
  border: 1px solid rgb(221 221 221 / 20%);
  margin: 15px;
  border-radius: 10px;
  -webkit-border-radius: 10px;
  -moz-border-radius: 10px;
  -ms-border-radius: 10px;
  -o-border-radius: 10px;
  width: 100%;
}

.info-box div {
  font-size: 32px;
  line-height: 1;
  padding-right: 15px;
  font-weight: 200;
}

.info-box div:last-of-type {
  padding-right: 0;
}

.info-box span {
  font-size: 22px;
  font-weight: 500;
  color: #999;
}

.green {
  background-color: #608e1c;
}

.blue {
  background-color: #1b4e7b;
}

@media screen and (min-width:800px) {
  .two-col {
    width: 50%;
  }
  
  .info-box {
    width: auto;
    min-width: 33%;
  }
}

